const JsyServer = require("../../../services/jsy-server.js");
const Util = require("../../../utils/util.js");
let _this, _customerId;
let buyType = ['梭织化纤面料', '梭织棉类面料', '经编化纤面料', '纬编化纤面料', '纬编棉类面料', '经编棉类面料', '其他面料'];
let sellType = ['梭织化纤坯布', '梭织纯棉坯布', '梭织混纺坯布', '梭织交织坯布', '梭织毛料坯布', '梭织麻类坯布', '梭织丝类坯布', '针织经编坯布', '针织纬编坯布', '经编棉类坯布', '其他坯布'];
// import keyboard from "./component/keyboard";

export default {
  data() {
    return {
      identity: '',
      //true买家，false卖家
      type: '',
      //选中的类型
      typeList: [],
      typeIndex: -1,
      //选中类型
      series: '',
      //系列
      newSeriesList: ['塔丝隆系列', '牛津系列', '尼丝纺系列', '仿记忆系列', '涤塔夫系列', '真记忆系列', '棉感系列', '桃皮绒系列', '仿真丝系列'],
      //系列最热表
      typeModal: false,
      //主营产品类型模态框
      peakSeason: '',
      //旺季时间
      peakSeasonList: [{
        name: '1月',
        option: false
      }, {
        name: '2月',
        option: false
      }, {
        name: '3月',
        option: false
      }, {
        name: '4月',
        option: false
      }, {
        name: '5月',
        option: false
      }, {
        name: '6月',
        option: false
      }, {
        name: '7月',
        option: false
      }, {
        name: '8月',
        option: false
      }, {
        name: '9月',
        option: false
      }, {
        name: '10月',
        option: false
      }, {
        name: '11月',
        option: false
      }, {
        name: '12月',
        option: false
      }],
      //时间列表
      timeModal: false,
      //旺季时间模态框
      newContentList: ['涤棉', '棉涤', '锦棉', '棉锦', '全涤', '全尼龙', '涤氨', '尼氨', '涤锦氨', '锦涤氨', '全棉', '锦涤'],
      //成分最热列表
      newStructureList: [],
      //风格最热列表
      photoList: [],
      //图片数组
      loadedImg: [],
      //已保存的图片
      specificationLong: '',
      //规格经
      specificationLat: '',
      //规格玮
      specailTab: 1,
      //特性
      swatchType: 0,
      //布样类型
      machineNum: '',
      //机台数量
      machineYield: '',
      //单机产量
      totalOutput: '',
      //日总产量
      productionStatus: 0,
      //生产状态0在机1下机
      quality: ["请选择", "一般订单", "市场订单", "高级订单", "品牌订单"],
      qualityIndex: 0,
      stockStatusList: ["请选择", "有现货", "可定制"],
      stockStatusIndex: 0,
      purchaseTab: 0,
      //购买数量单位
      disabled: false,
      //是否能改产品和系列
      update: false //是否是更新

    };
  },

  onLoad: function (options) {
    _this = this;
    _customerId = options.customerId;
    let typeList;

    if (options.type == 2) {
      typeList = buyType;
      this.setData({
        identity: true,
        typeList: buyType
      });
    } else {
      typeList = sellType;
      this.setData({
        identity: false,
        typeList: sellType
      });
    }

    if (options.types) {
      let typeIndex = -1;
      typeList.map((item, indexs) => {
        if (item == options.types) {
          typeIndex = indexs;
        }
      });
      this.setData({
        typeIndex,
        series: options.series,
        disabled: true
      });
    }

    if (options.odata) {
      let detail = JSON.parse(options.odata);
      this.setOdata(detail);
    }

    if (options.update) {
      this.setData({
        update: true
      });
    }

    console.log("options--", options); // this.getNewSearch("series");
    // this.getNewSearch("content");

    this.getNewSearch("structure");
  },
  components: {
   
  },
  props: {},
  methods: {
    // 设置原始数据
    setOdata: function (data) {
      console.log("data===", data);
      let typeList = this.typeList;
      let typeIndex = -1;
      let peakSeasonList = this.peakSeasonList;
      let peakSeasonArr = data.peakSeason.split(",");
      typeList.map((item, indexs) => {
        if (item == data.type) {
          typeIndex = indexs;
        }
      });
      peakSeasonList.map(item => {
        let whether = false;

        for (let i = 0; i <= peakSeasonArr.length; i++) {
          if (item.name == peakSeasonArr[i]) {
            console.log("for==", peakSeasonArr[i]);
            whether = true;
          }
        }

        item.option = whether;
        return item;
      });
      this.setData({
        typeIndex,
        series: data.series,
        peakSeasonList,
        name: data.name,
        peakSeason: data.peakSeason,
        content: data.info.content,
        containLong: data.info.containLong,
        containLat: data.info.containLat,
        specificationLong: data.info.specificationLong,
        specificationLat: data.info.specificationLat,
        specailTab: data.info.glossLongitude,
        structure: data.info.structure,
        organization: data.info.organization,
        swatchType: data.info.prodOrGrey,
        number1: data.info.prodOrGrey == 0 ? data.info.greyDensityLong : data.info.prodDensityLong,
        number2: data.info.prodOrGrey == 0 ? data.info.greyDensityLat : data.info.prodDensityLat,
        width: data.info.prodOrGrey == 0 ? data.info.greyWidth : data.info.prodWidth,
        weight: data.info.prodOrGrey == 0 ? data.info.greyWeight : data.info.prodWeight,
        qualityIndex: data.positionQuality,
        productionStatus: data.productionStatus,
        machineNum: data.machineNum,
        machineYield: data.machineYield,
        totalOutput: data.totalOutput,
        stockStatusIndex: data.stockStatus,
        num: data.customerType == 2 ? data.demandNum : data.stockNum,
        purchaseTab: data.unit == "米" ? 0 : 1,
        photoList: data.picsList,
        loadedImg: data.pics,
        productId: data.productId,
        id: data.id
      });
    },
    // 获取系列风格最热搜索
    getNewSearch: function (index) {
      let data = {
        type: index,
        num: 20
      };
      JsyServer.getNewNumber(data).then(res => {
        if (index == "content") {
          _this.setData({
            newContentList: res.data
          });
        } else if (index == "structure") {
          _this.setData({
            newStructureList: res.data
          });
        } else if (index == "series") {
          _this.setData({
            newSeriesList: res.data
          });
        }
      }).catch(err => {
        console.log("err===", err);
      });
    },
    // 选择最热
    changeSeries: function (e) {
      this.setData({
        series: this.newSeriesList[e.detail.value]
      });
    },
    changeContent: function (e) {
      this.setData({
        content: this.newContentList[e.detail.value]
      });
    },
    changeStructure: function (e) {
      this.setData({
        structure: this.newStructureList[e.detail.value].structure
      });
    },
    // 选择选项
    typeOption: function (e) {
      let index = e.currentTarget.dataset.index;
      let type = e.currentTarget.dataset.type;

      if (type == 'time') {
        let peakSeasonList = this.peakSeasonList;
        this.setData({
          [`peakSeasonList[${index}].option`]: !peakSeasonList[index].option
        });
      } else {
        let typeList = this.typeList;
        this.setData({
          [`typeList[${index}].option`]: !typeList[index].option
        });
      }
    },
    // 确定
    sureModal: function (e) {
      let peakSeasonList = this.peakSeasonList;
      let peakSeasonArr = [];
      peakSeasonList.map(item => {
        if (item.option) {
          peakSeasonArr.push(item.name);
        }
      });
      let peakSeason = peakSeasonArr.join(",");
      this.setData({
        peakSeason,
        timeModal: false
      });
    },
    // 打开选择模态框
    choiceTime: function (e) {
      this.setData({
        timeModal: true
      });
    },
    // 关闭模态框
    cancelMoadl: function (e) {
      let isType = e.currentTarget.dataset.type;
      let whether;
      let peakSeasonList = this.peakSeasonList;
      let peakSeason = this.peakSeason;
      let peakSeasonArr = peakSeason.split(',');
      peakSeasonList.map(item => {
        whether = false;

        for (let i = 0; i <= peakSeasonArr.length; i++) {
          if (item.name == peakSeasonArr[i]) {
            console.log("for==", peakSeasonArr[i]);
            whether = true;
          }
        }

        item.option = whether;
        return item;
      });
      this.setData({
        timeModal: false,
        peakSeasonList
      });
    },
    //选择特性
    changeSpecail: function (e) {
      this.setData({
        specailTab: e.currentTarget.dataset.tab
      });
    },
    //选择布样类型
    bindswatchType: function (e) {
      this.setData({
        swatchType: e.currentTarget.dataset.index
      });
    },
    // 选择生产状态
    bindProductionStatus: function (e) {
      this.setData({
        productionStatus: e.currentTarget.dataset.index
      });
    },
    // 购买数量单位
    changePurchase: function (e) {
      this.setData({
        purchaseTab: e.currentTarget.dataset.tab
      });
    },
    //选择品质定位
    choiceQuality: function (e) {
      this.setData({
        qualityIndex: e.detail.value
      });
    },
    // 选择主营产品类型
    choiceType: function (e) {
      this.setData({
        typeIndex: e.detail.value
      });
    },
    // 选择库存状态
    choicestockStatus: function (e) {
      this.setData({
        stockStatusIndex: e.detail.value
      });
    },
    // 输入机台数量
    changeMachineNum: function (e) {
      let num = e.detail.value;
      let machineYield = this.machineYield;
      let totalOutput = this.totalOutput;

      if (machineYield) {
        totalOutput = (num * machineYield).toFixed(2);
      }

      this.setData({
        machineNum: num,
        totalOutput
      });
    },
    // 输入单机产量
    changeMachineYield: function (e) {
      let num = e.detail.value;
      let machineNum = this.machineNum;
      let totalOutput = this.totalOutput;

      if (machineNum) {
        totalOutput = (num * machineNum).toFixed(2);
      }

      this.setData({
        machineYield: num,
        totalOutput
      });
    },
    bindCancel: function () {
      wx.navigateBack({});
    },
    formSubmit: function (e) {
      let form = e.detail.value;
      let info = {
        content: form.content,
        containLong: form.containLong,
        containLat: form.containLat,
        organization: form.organization,
        specificationLong: form.specificationLong,
        specificationLat: form.specificationLat,
        glossLongitude: this.specailTab,
        structure: form.structure,
        prodOrGrey: this.swatchType,
        positionQuality: this.qualityIndex
      };

      if (this.swatchType == 0) {
        info.greyDensityLong = form.number1;
        info.greyDensityLat = form.number2;
        info.greyWidth = form.width || 160;
        info.greyWeight = form.weight;
      } else {
        info.prodDensityLong = form.number1;
        info.prodDensityLat = form.number2;
        info.prodWidth = form.width || 160;
        info.prodWeight = form.weight;
      }

      let type = this.typeIndex == -1 ? '' : this.typeList[this.typeIndex];
      let data = {
        info,
        customerId: _customerId,
        customerType: this.identity ? 2 : 4,
        type,
        series: form.series,
        name: form.name,
        peakSeason: this.peakSeason,
        positionQuality: this.qualityIndex,
        unit: this.purchaseTab == 0 ? '米' : '码'
      };

      if (!this.identity) {
        data.productionStatus = this.productionStatus;
        data.stockNum = form.num;
        data.stockStatus = this.stockStatusIndex;

        if (this.productionStatus == 0) {
          data.machineNum = parseFloat(form.machineNum);
          data.machineYield = parseFloat(form.machineYield);
          data.totalOutput = parseFloat(form.totalOutput);
        }
      } else {
        data.demandNum = form.num;
      }

      console.log("----", this.id);
      console.log("----", !this.update);

      if (this.id && !this.update) {
        data.id = this.id;
      }

      if (!type) {
        wx.showToast({
          title: '请选择主营产品类型',
          icon: 'none'
        });
        return;
      }

      if (!form.series) {
        wx.showToast({
          title: '请输入主营产品系列',
          icon: 'none'
        });
        return;
      }

      if (!form.name) {
        wx.showToast({
          title: '请输入主营产品名称',
          icon: 'none'
        });
        return;
      }

      if (!this.peakSeason) {
        wx.showToast({
          title: '请选择旺季时间',
          icon: 'none'
        });
        return;
      }

      if (!form.content) {
        wx.showToast({
          title: '请输入成份',
          icon: 'none'
        });
        return;
      }

      if (!form.structure) {
        wx.showToast({
          title: '请输入风格',
          icon: 'none'
        });
        return;
      }

      if (!form.specificationLong || !form.specificationLat) {
        wx.showToast({
          title: '请输入规格',
          icon: 'none'
        });
        return;
      }

      if (!form.number1 || !form.number2) {
        wx.showToast({
          title: '请输入密度',
          icon: 'none'
        });
        return;
      }

      if (this.qualityIndex == 0) {
        wx.showToast({
          title: '请选择品质定位',
          icon: 'none'
        });
        return;
      }

      if (!this.identity && this.productionStatus == 0) {
        if (!form.machineNum) {
          wx.showToast({
            title: '请输入机台数量',
            icon: 'none'
          });
          return;
        }

        if (!form.machineYield) {
          wx.showToast({
            title: '请输入单机产量',
            icon: 'none'
          });
          return;
        }
      }

      if (!this.identity) {
        if (this.stockStatusIndex == 0) {
          wx.showToast({
            title: '请选择库存状态',
            icon: 'none'
          });
          return;
        }

        if (!form.num) {
          wx.showToast({
            title: '请输入库存数量',
            icon: 'none'
          });
          return;
        }
      } else {
        if (!form.num) {
          wx.showToast({
            title: '请输入需求数量',
            icon: 'none'
          });
          return;
        }
      }

      console.log("data===", data);
      this.onSubImg(data);
    },
    // 提交表单图片
    onSubImg: function (oData) {
      let that = this;
      let imgArr = that.photoList;
      var newImgArr = Util.picUpPic(imgArr, this.loadedImg);
      wx.showLoading({
        title: '提交中...'
      });

      if (imgArr.length > 0) {
        Util.uploadPics(newImgArr[0]).then(res => {
          oData.pics = newImgArr[1].concat(res);
          that.onSubFind(oData);
        }).catch(err => {
          console.log("err==", err);
          wx.hideLoading();
          Util.showErrorToast('图片提交失败');
        });
      } else {
        that.onSubFind(oData);
      }
    },
    onSubFind: function (data) {
      console.log("data---", data);

      if (this.update) {
        JsyServer.updateProduct(data, this.productId).then(res => {
          console.log("11111====", res);
          var pages = getCurrentPages();
          var lastpage = pages[pages.length - 2];
          lastpage.setData({
            refresh: true,
            id: res.data
          });
          wx.showToast({
            title: '更新成功'
          });
          let timer = setTimeout(() => {
            wx.navigateBack({});
          }, 1000);
        }).catch(err => {
          console.log("err=>", err);
        });
      } else {
        JsyServer.addProduct(data).then(res => {
          var pages = getCurrentPages();
          var lastpage = pages[pages.length - 2];
          lastpage.setData({
            refresh: true
          });

          if (_this.id) {
            wx.showToast({
              title: '修改成功'
            });
          } else {
            wx.showToast({
              title: '添加成功'
            });
          }

          let timer = setTimeout(() => {
            wx.navigateBack({});
          }, 1000);
        }).catch(err => {
          console.log("err=>", err);
        });
      }
    },
    //预览图片
    previewImage: function (e) {
      let index = e.currentTarget.dataset.index;
      let imgList = this.photoList;
      wx.previewImage({
        current: imgList[index],
        // 当前显示图片的http链接
        urls: imgList // 需要预览的图片http链接列表

      });
    },
    //删除图片
    delImage: function (e) {
      let i = e.currentTarget.dataset.index;
      let delList = this.photoList;
      delList.splice(i, 1);
      this.setData({
        photoList: delList
      });
    },
    //添加图片
    chooseImage: function (e) {
      let that = this;
      wx.chooseImage({
        sizeType: ['original', 'compressed'],
        // 可以指定是原图还是压缩图，默认二者都有
        sourceType: ['album', 'camera'],
        // 可以指定来源是相册还是相机，默认二者都有
        success: function (res) {
          console.log("res===", res);
          let newArr = [];
          let filesArr = res.tempFiles;
          let fliesPathArr = res.tempFilePaths;
          let resArr = [];
          let maxSize = 1024 * 1024;

          if (!filesArr) {
            // 用于处理部分手机返回值为空的问题
            Util.versionTip();
            return;
          } // 验证size是否大于1M


          filesArr.map(function (item, index) {
            let size = item.size;

            if (size > maxSize) {
              fliesPathArr.splice(index, 1);
              wx.showModal({
                content: '图片不能大于1M',
                showCancel: false
              });
            }
          });
          resArr = that.photoList.concat(fliesPathArr); // 验证图片地址是否重复

          resArr.map(function (item) {
            if (newArr.indexOf(item) == -1) {
              newArr.push(item);
            } else {
              wx.showModal({
                content: '不能提交同一张图片',
                showCancel: false,
                success: function () {
                  return false;
                }
              });
            }
          }); // 判断图片数量是否大于9

          if (newArr.length > 9) {
            newArr.splice(9);
            wx.showModal({
              title: '提示',
              content: '照片最多为9张',
              showCancel: false,
              success: function () {
                return false;
              }
            });
          } // 返回选定照片的本地文件路径列表，tempFilePath可以作为img标签的src属性显示图片


          that.setData({
            photoList: newArr
          });
        },
        fail: function () {
          console.log("已取消图片选择");
        }
      });
    },
    //虚拟键盘
    bindKeyboard: function (e) {
      let inputValue;
      let keyboardIndex = this.keyboardIndex;

      if (keyboardIndex == 0) {
        inputValue = this.specificationLong;
      } else {
        inputValue = this.specificationLat;
      }

      console.log("1111=", e.detail);

      if (e.detail == '删除') {
        inputValue = inputValue.substring(0, inputValue.length - 1);
      } else if (e.detail == 'top') {
        this.setData({
          keyboardState: false
        });
      } else if (e.detail == '空格') {
        inputValue += " ";
      } else {
        inputValue += e.detail;
      }

      if (keyboardIndex == 0) {
        this.setData({
          specificationLong: inputValue
        });
      } else {
        this.setData({
          specificationLat: inputValue
        });
      }
    },
    //拉起虚拟键盘
    inputValue: function (e) {
      let index = e.currentTarget.dataset.index;
      console.log("index=", index);
      this.setData({
        keyboardState: true,
        keyboardIndex: index
      });

      if (this.keyboardState) {
        wx.pageScrollTo({
          scrollTop: 400
        });
      }
    },
    setData: function (obj) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
    }
  }
};
